  -moz-box-shadow: inset 0 0 200px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: inset 0 0 200px rgba(0, 0, 0, 0.1);
  box-shadow: inset 0 0 200px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  */
}

h1 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 2.75em;
  line-height: 1.25em;
  margin: 0;
}

h2 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 2em;
  line-height: 1.3em;
  margin: 0;
}

h3 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 1em;
  line-height: 1.2em;
  letter-spacing: 3px;
  text-transform: uppercase;
  margin: 0;
}

h4 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 1.2em;
  line-height: 1.5em;
  margin: 0;
}

h5 {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1em;
  line-height: 1.5em;
  margin: 0;
}

p {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1.2em;
  line-height: 1.5em;
  margin: 0;
}

a {
