/*     NAVBAR     */
#nav {
  position: fixed;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 0.8em;
  line-height: 2.5em;
  letter-spacing: 3px;
  text-align: center;
  list-style: none;
  margin: 0;
  padding: 0;
  z-index: 100;
}

#nav li a {
  text-decoration: none;
  color: rgba(0, 0, 0, 1);
  cursor: pointer;
}

#nav li a:hover {
  color: rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

#nav-top {
  height: 2.5em;
  width: 100vw;
  position: fixed;
  top: 0;
  z-index: 100;
  background-color: rgba(255, 255, 255, 1);
}

#nav-right {
  height: 2.5em;
  width: 100vh;
  position: fixed;
  transform: rotate(90deg);
  transform-origin: left top;
  left: 100%;
  z-index: 100;
  background-color: rgba(255, 255, 255, 1);
}

#nav-bottom {
  height: 2.5em;
  width: 100vw;
  position: fixed;
  bottom: 0;
  z-index: 100;
  background-color: rgba(255, 255, 255, 1);
}

#nav-left {
  height: 2.5em;
  width: 100vh;
  position: fixed;
  transform: rotate(-90deg);
  transform-origin: right top;
  right: 100%;
  z-index: 100;
  background-color: rgba(255, 255, 255, 1);
}





/*     PROJECTS     */
#title {
  background-color: #DFDFDF;
  padding-top: 2.5em;
  padding-left: 3.5em;
  height: 2.5em;
  font-family: "Poppins", sans-serif;
  font-size: 0.8em;
  line-height: 2.6em;
  z-index: 0;
}

.scrollbar {
  position: relative;   
  padding: 0;   
  margin: 0;
}

#project-page {
  min-height: 100vh;
  padding: 4em 6em 6em 6em;
  box-sizing: border-box;
  z-index: 1;
}

  /* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
  #project-page {
    padding: 2.5em 2.5em 2.5em 2.5em;
  }
}

.project-title {
  position: fixed;  
  width: 100%;
}

.project-caption-third {
  width: 33%;
  display: inline-block;
  vertical-align: top;
  padding-right: 2.5em;
  box-sizing: border-box;
}

.project-caption-half {
  width: 50%;
  display: inline-block;
  vertical-align: top;
  padding-right: 2.5em;
  box-sizing: border-box;
}

.project-caption {
  width: 100%;
}

.project-text-half {
  width: 50%;
  display: inline-block;
  vertical-align: top;
  padding: 0 2.5em 0 2.5em;
  box-sizing: border-box;
}

  /* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
  .project-text-half {
    width: 100%;
  }

  .project-caption-third {
    width: 100%;
  }
}

.project-text {
  width: 80%;
  padding: 0 2.5em 0 2.5em;
}

img {
  padding: 0 2.5em 0 2.5em;
  width: 100%;
  height: auto;
  box-sizing: border-box;
}

video {
  padding: 0 2.5em 0 2.5em;
  width: 100%;
  height: auto;
  box-sizing: border-box;
}

.padding-top-05em {
  padding-top: 0.5em;
}

.padding-top-1em {
  padding-top: 1em;
}

.padding-top-2em {
  padding-top: 2em;
}

.padding-top-3em {
  padding-top: 3em;
}





/*     PROJECT GRID     */
